











































































































import { Component, Prop, Vue } from 'vue-property-decorator';

import HeaderSearch from './Header.Search.vue';
import HeaderNotify from './Header.Notify.vue';
import HeaderUser from './Header.User.vue';

@Component({
  components: {
    HeaderSearch,
    HeaderNotify,
    HeaderUser,
  },
})
export default class Header extends Vue {

  private isCollapsed: boolean = false;

  private store: any = this.$store;

  private appState = this.$store.state.app;

  private collpasedName(): string {
    return this.isCollapsed ? 'indent' : 'outdent';
  }

  private handlerCollapsedSidebar( ): void {
    this.store.dispatch('collapse');
  }

}
