const appState = {
    state: {
      layout: 'admin',
      isCollapse: false,
      routerLoading: false,
    },
    mutations: {
      collapse(state: any) {
        state.isCollapse = !state.isCollapse;
      },
      routerLoading(state: any) {
        state.routerLoading = true;
      },
      routerUnLoading(state: any) {
        state.routerLoading = false;
      },
    },
    actions: {
      collapse(context: any) {
        context.commit('collapse');
      },
      routerLoad(context: any, param: any) {
        if ( param.loading ) {
          context.commit('routerLoading');
        } else {
          context.commit('routerUnLoading');
        }
      },
    },
  };


export default appState;
