








































































import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({})
export default class ReuseTab extends Vue {

    private state = this.$store.state.routerTables;

    constructor() {
        super();
    }

    get canClose(): boolean {
        const can = this.state.source.length !== 1;
        return can;
    }

    get routerTables(): any[] {
        return this.state.source;
    }

    private handlerRemoveTab(targetName: string) {
        this.$store.dispatch('remove', {
            name: targetName,
        });
    }

    private handlerTabClick(tab: any) {
        this.$router.push(tab.name);
    }

}
